const storage = window.localStorage;

class LocalStorage {
  static isItemExpired(key) {
    const date = JSON.parse(storage.getItem(`${key}_date`));

    return (date === undefined) || ((Date.now() - date) > (1000 * 60 * 60 * 24));
  }

  static updateItemDate(key) {
    storage.setItem(`${key}_date`, JSON.stringify(Date.now()));
  }

  static storeKiosks(data) {
    storage.setItem('kiosks', JSON.stringify(data));
    LocalStorage.updateItemDate('kiosks');
  }

  static getKiosks() {
    if (LocalStorage.isItemExpired('kiosks')) return null;
    return JSON.parse(storage.getItem('kiosks'));
  }

  static storeRetailers(data) {
    storage.setItem('retailers', JSON.stringify(data));
    LocalStorage.updateItemDate('retailers');
  }

  static getRetailers() {
    if (LocalStorage.isItemExpired('retailers')) return null;
    return JSON.parse(storage.getItem('retailers'));
  }
}


export default LocalStorage;
