import http from 'src/services/network';
import { storePageConfig } from 'src/services/pages/actions';
import { pagesSelector } from 'src/services/pages/selectors';
import { recaptchaKeySelector } from 'src/services/options/selectors';


const fetchPageConfig = (pageName) => {
  const url = `${SERVER_URL}/pages/?name=${pageName}`;

  return http.get(url)
    .then(res => res);
};

const fetchFormConfig = (formId) => {
  const url = `${SERVER_URL}/forms?_id=${formId}`;

  return http.get(url)
    .then(res => res);
};

export const fetchAndStorePageConfig = pageName => (dispatch, getState) => {
  const pageConfig = pagesSelector(getState())[pageName];
  if (pageConfig !== undefined) return Promise.resolve();

  return fetchPageConfig(pageName)
    .then((res) => {
      const { form } = res.data[0];

      // If a page with form - fetch form data,
      // because the page config doesn't contain form attributes
      if (form) {
        return fetchFormConfig(form._id)
          .then((formRes) => {
            res.data[0].form = formRes.data[0];
            return res;
          });
      } return res;
    })
    .then((response) => {
      const data = response.data[0];
      // Test data
      // if (pageName === 'AboutUs') {
      //   data.buttons.push(data.buttons[0])
      // }
      // if (pageName === 'Main') {
      //   data.buttons = data.buttons.slice(0, 5);
      // }


      dispatch(storePageConfig(pageName, data));
    });
};


export const loadRecaptcha = () => (dispatch, getState) => {
  if (document.getElementById('recaptcha-api')) return;

  const key = recaptchaKeySelector(getState());
  const onLoad = () => {
    grecaptcha.ready(() => {
    });
  };

  const script = document.createElement('script');

  script.onload = onLoad;
  script.async = true;
  script.defer = true;
  script.id = 'recaptcha-api';
  script.src = `https://www.google.com/recaptcha/api.js?render=${key}`;

  document.body.appendChild(script);
};
